import {Component, OnInit} from '@angular/core';
import {LocalDataSource} from 'ng2-smart-table';
import {SingleService} from '../single.service';
import {SingleTableService} from '../single-table.service';
import {RENTENTION, Retention} from './retention';
import * as moment from 'moment';

declare var $;


@Component({
  selector: 'ngx-retention',
  templateUrl: './retention.component.html',
  styleUrls: ['./retention.component.scss'],
})
export class RetentionComponent implements OnInit {
  options: any = {};
  retention: Retention = RENTENTION;
  mInstance: any;
  title: string;

  chartInit(ev): void {
    this.mInstance = ev;
    this.singleService.changeActiveData(this.singleService.getTimeStamp());
  }

  constructor(private service: SingleTableService, private singleService: SingleService) {
    this.title = this.singleService.getTimeStamp().appName;
    const data = this.service.getData();
    this.source.load(data);
  }

  ngOnInit() {
    this.dateRangePicker();
    this.options = {
      /*selected: {
        text: '多个背景颜色图',
        x: 'left',
      },*/
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          animation: false,
        },
        formatter: function (params) {
          return params[0].name + '<br/>'
            + '保留率 : ' + params[0].value + ' %<br/>';
        },
      },
      legend: {
        data: ['流量'],
        x: 'left',
      },
      toolbox: {
        feature: {
          dataZoom: {
            yAxisIndex: 'none',
          },
          restore: {},
          saveAsImage: {},
        },
      },
      axisPointer: {
        link: {
          xAxisIndex: 'all',
        },
      },
      dataZoom: [{
        show: true,
        realtime: true,
        start: 30,
        end: 70,

        xAxisIndex: [0, 1],
      }, {
        type: 'inside',
        realtime: true,
        start: 30,
        end: 70,
        xAxisIndex: [0, 1],
      }],
      grid: [{
        left: 40,
        right: 40,
      }, {
        left: 40,
        right: 40,
      }],
      xAxis: [{
        type: 'category',
        boundaryGap: false,
        axisLine: {
          onZero: true,
        },
        data: this.retention.data,
      }, {
        gridIndex: 1,
      }],
      yAxis: [{
        type: 'value',
        axisLabel: {
          formatter: '{value} %',
        },
      }, {
        gridIndex: 1,
      }],
      series: [{
        name: '数值',
        type: 'line',
        smooth: true,
        symbol: 'circle',
        symbolSize: 9,
        showSymbol: false,
        lineStyle: {
          normal: {
            width: 1,
          },
        },
        markPoint: {
          data: [{
            type: 'max',
            name: '最大值',
          }, {
            type: 'min',
            name: '最小值',
          }],
        },
        markArea: {
          silent: true,
          label: {
            normal: {
              position: ['10%', '50%'],
            },
          },
        },
        data: this.retention.seriesData,

      }],
    };
  }

  dateRangePicker() {
    const picker: any = $('#startEndTime');
    const dataRageOption: Object = {
      locale: {
        applyClass: 'btn-green',
        applyLabel: '确定',
        cancelLabel: '取消',
        fromLabel: '开始',
        format: 'YYYY/MM/DD',
        toLabel: '到',
        customRangeLabel: '自定义时间',
        daysOfWeek: ['日', '一', '二', '三', '四', '五', '六'],
        firstDay: 1,
        monthNames: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
      },
      startDate: moment().startOf('month'),
      endDate: moment(),
      maxDate: moment(),
      // minDate: moment().subtract(90, 'days'),
      'ranges': {
        '今日': [
          moment(),
          moment(),
        ],
        '昨日': [
          moment().subtract(1, 'days'),
          moment().subtract(1, 'days'),
        ],
        '本周': [moment().subtract(7, 'days'), moment().subtract(1, 'days')],
        '最近30天': [moment().subtract(30, 'days'), moment().subtract(1, 'days')],
        '本月': [moment().startOf('month'), moment().endOf('month')],
        '上个月': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')],
      },
      'alwaysShowCalendars': true,
      'showCustomRangeLabel': false,
    };
    picker.daterangepicker(dataRageOption, function (start, end, label) {
      console.info(`start:${start.format('YYYY/MM/DD')}, end:${end}, label:${label}`);
    });

  }

  settings = {
    actions: false,
    hideSubHeader: true,
    columns: {
      id: {
        title: 'ID',
        type: 'number',
      },
      firstName: {
        title: '日期',
        type: 'string',
      },
      lastName: {
        title: '启动用户',
        type: 'number',
      },
      username: {
        title: '日活跃度',
        type: 'string',
      },
      email: {
        title: '周活跃用户',
        type: 'number',
      },
      age: {
        title: '周活跃度',
        type: 'string',
      },
      mon: {
        title: '月活跃用户',
        type: 'number',
      },
      month: {
        title: '月活跃度',
        type: 'string',
      },
    },
  };

  source: LocalDataSource = new LocalDataSource();
}



