import {AfterViewInit, Component} from '@angular/core';
import {RENTENTION, Retention} from '../retention/retention';

@Component({
  selector: 'ngx-single-retention',
  template: `
    <div echarts [options]="options" theme = "light" class="echart"></div>
  `,
})
export class SingleRetentionComponent implements AfterViewInit {
  options: any = {};
  retention: Retention = RENTENTION;

  ngAfterViewInit() {
    this.options = {
      /*selected: {
        text: '多个背景颜色图',
        x: 'left',
      },*/
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          animation: false,
        },
        formatter: function (params) {
          return params[0].name + '<br/>'
            + '保留率 : ' + params[0].value + ' %<br/>';
        },
      },
      legend: {
        data: ['流量'],
        x: 'left',
      },
      /*toolbox: {
        feature: {
          dataZoom: {
            yAxisIndex: 'none',
          },
          restore: {},
          saveAsImage: {},
        },
      },*/
      axisPointer: {
        link: {
          xAxisIndex: 'all',
        },
      },
      grid: [{
        left: 40,
        right: 40,
      }, {
        left: 40,
        right: 40,
      }],
      xAxis: [{
        type: 'category',
        boundaryGap: false,
        axisLine: {
          onZero: true,
        },
        data: this.retention.data,
      }, {
        gridIndex: 1,
      }],
      yAxis: [{
        type: 'value',
        axisLabel: {
          formatter: '{value} %',
        },
      }, {
        gridIndex: 1,
      }],
      series: [{
        name: '保留率',
        type: 'line',
        smooth: true,
        symbol: 'circle',
        symbolSize: 9,
        showSymbol: false,
        lineStyle: {
          normal: {
            width: 1,
          },
        },
        markPoint: {
          data: [{
            type: 'max',
            name: '最大值',
          }, {
            type: 'min',
            name: '最小值',
          }],
        },
        markArea: {
          silent: true,
          label: {
            normal: {
              position: ['10%', '50%'],
            },
          },
        },
        data: this.retention.seriesData,

      }],
    };
  }
}



