import {Component, OnInit} from '@angular/core';
import {LocalDataSource} from 'ng2-smart-table';
import {SingleTableService} from '../single-table.service';
import * as moment from 'moment';
import {SingleService} from '../single.service';
import {DatePipe} from '@angular/common';
import {REQDATA, ReqData, SERIESDATA, SeriesData} from '../single';

declare var $;

@Component({
  selector: 'ngx-daytime',
  templateUrl: './daytime.component.html',
  styleUrls: ['./daytime.component.scss'],
})
export class DaytimeComponent implements OnInit {
  options: any = {};
  seriesData: SeriesData = SERIESDATA;
  mInstance: any;
  title: string ;

  labelFromatter = {
    normal: {
      label: {
        formatter: function (params) {
          return params.value + '%';
        },
        textStyle: {
          baseline: 'top',
        },
      },
    },
  };

  chartInit(ev): void {
    this.mInstance = ev;
    /*const date = new Date();
    const pipe = new DatePipe('en-US');
    this.reqData.reqTime = pipe.transform(date, 'yyyy-MM-dd');
    this.singleService.changeDate(this.reqData);*/
    this.singleService.changeDate(this.singleService.getReqData());
  }

  constructor(private service: SingleTableService, private singleService: SingleService) {
    this.title = this.singleService.getReqData().appName;
    const data = this.service.getData();
    this.source.load(data);
    this.singleService.getDateSubject().subscribe(x => {
      this.singleService.getDayTime(x).then(z => {
        this.seriesData = z as SeriesData;
        this.options.legend.data = this.seriesData.time;
        let k = 0;
        for (const se of this.seriesData.data) {
          this.options.series[k].data = se;
          k++;
        }
        this.mInstance.setOption(this.options);
      });
    });
  }

  ngOnInit() {
    this.dateRangePicker();
    const radius = [40, 55];
    this.options = {
      tooltip: {
        formatter: function (params) {
          return params.name + ':' + params.value + '%\n';
        },
      },
      legend: {
        x: 'center',
        y: '20%',
        data: this.seriesData.time,
      },
      toolbox: {
        show: true,
        feature: {
          dataView: {show: true, readOnly: false},
          magicType: {
            show: true,
            type: ['pie', 'funnel'],
            option: {
              funnel: {
                width: '20%',
                height: '30%',
                itemStyle: {
                  normal: {
                    label: {
                      formatter: function (params) {
                        return 'other\n' + params.value + '%\n';
                      },
                      textStyle: {
                        baseline: 'middle',
                      },
                    },
                  },
                },
              },
            },
          },
          restore: {show: true},
          saveAsImage: {show: true},
        },
      },
      series: [
        {
          type: 'pie',
          center: ['10%', '50%'],
          radius: radius,
          x: '0%', // for funnel
          itemStyle: this.labelFromatter,
          data: this.seriesData.data[0],
        },
        {
          type: 'pie',
          center: ['30%', '50%'],
          radius: radius,
          x: '20%', // for funnel
          itemStyle: this.labelFromatter,
          data: this.seriesData.data[4],
        },
        {
          type: 'pie',
          center: ['50%', '50%'],
          radius: radius,
          x: '40%', // for funnel
          itemStyle: this.labelFromatter,
          data: this.seriesData.data[1],
        },
        {
          type: 'pie',
          center: ['70%', '50%'],
          radius: radius,
          x: '60%', // for funnel
          itemStyle: this.labelFromatter,
          data: this.seriesData.data[2],
        },
        {
          type: 'pie',
          center: ['90%', '50%'],
          radius: radius,
          x: '80%', // for funnel
          itemStyle: this.labelFromatter,
          data: this.seriesData.data[3],
        },
      ],
    };
  }

  dateRangePicker() {
    const picker: any = $('#startEndTime');
    const dataRageOption: Object = {
      singleDatePicker: true,
      showDropdowns: true,
      locale: {
        applyClass: 'btn-green',
        applyLabel: '确定',
        cancelLabel: '取消',
        daysOfWeek: ['日', '一', '二', '三', '四', '五', '六'],
        firstDay: 1,
        monthNames: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
        format: 'YYYY-MM-DD',
      },
      maxDate: moment(),
    };
    picker.daterangepicker(dataRageOption, (start, end, label) => {
      console.info(`start:${start.format('YYYY-MM-DD')}, end:${end}, label:${label}`);
      const reqData = new ReqData();
      reqData.reqTime = start.format('YYYY-MM-DD');
      this.singleService.changeDate(reqData);
    });

  }

  settings = {
    actions: false,
    hideSubHeader: true,
    columns: {
      id: {
        title: 'ID',
        type: 'number',
      },
      firstName: {
        title: '日期',
        type: 'string',
      },
      lastName: {
        title: '启动用户',
        type: 'number',
      },
      username: {
        title: '日活跃度',
        type: 'string',
      },
      email: {
        title: '周活跃用户',
        type: 'number',
      },
      age: {
        title: '周活跃度',
        type: 'string',
      },
      mon: {
        title: '月活跃用户',
        type: 'number',
      },
      month: {
        title: '月活跃度',
        type: 'string',
      },
    },
  };

  source: LocalDataSource = new LocalDataSource();
}



