import {Injectable} from '@angular/core';
import {Subject} from 'rxjs/Subject';
import {HttpClient, HttpParams} from '@angular/common/http';
import {ReqData, TimeStamp} from './single';
import * as moment from 'moment';

@Injectable()
export class SingleService {
  private BaseUrl = 'http://10.0.0.30:8000/';
  private dateSubject = new Subject<ReqData>();
  private dataSubject = new Subject<TimeStamp>();
  private timeStamp: TimeStamp;
  private reqData: ReqData;

  constructor(private http: HttpClient) {
    this.timeStamp = new TimeStamp();
    this.timeStamp.startTime = moment().startOf('month').format('YYYY-MM-DD');
    this.timeStamp.endTime = moment().format('YYYY-MM-DD');
    this.timeStamp.appName = '王者荣耀';
    this.reqData = new ReqData();
    this.reqData.reqTime = moment().format('YYYY-MM-DD');
    this.reqData.appName = '王者荣耀';
  }

  getTimeStamp(): TimeStamp {
    return this.timeStamp;
  }

  getReqData(): ReqData {
    return this.reqData;
  }

  getAppNames() {
    const url = this.BaseUrl + 'listAllAppName';
    return this.http.get(url).toPromise();
  }

  changeApp(app: string) {
    this.timeStamp.appName = app;
    this.reqData.appName = app;
    this.dataSubject.next(this.timeStamp);
    this.dateSubject.next(this.reqData);
  }

  changeDate(req: ReqData): void {
    this.reqData.reqTime = req.reqTime;
    this.dateSubject.next(this.reqData);
  }

  getDateSubject(): Subject<ReqData> {
    return this.dateSubject;
  }

  changeActiveData(timeStamp: TimeStamp): void {
    this.timeStamp.startTime = timeStamp.startTime;
    this.timeStamp.endTime = timeStamp.endTime;
    this.dataSubject.next(this.timeStamp);
  }

  getActiveSubject(): Subject<TimeStamp> {
    return this.dataSubject;
  }


  getDayTime(reqData: ReqData) {
    const url = this.BaseUrl + 'listDayUseFrequencyByApp';
    const params = new HttpParams().set('appName', reqData.appName).append('day', reqData.reqTime);
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }

  getUserTime(reqData: ReqData) {
    const url = this.BaseUrl + 'getSingleAppOnceHourCountByPacketAndDate';
    const params = new HttpParams().set('appName', reqData.appName).append('day', reqData.reqTime);
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }

  getUserActive(timeStamp: TimeStamp) {
    const url = this.BaseUrl + 'listLivenessByAppAndDate';
    const params = new HttpParams().set('appName', timeStamp.appName).append('start', timeStamp.startTime)
      .append('end', timeStamp.endTime);
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }

  getVersion(timeStamp: TimeStamp) {
    const url = this.BaseUrl + 'getAppVersionMsgByAppAndDate';
    const params = new HttpParams().set('appName', timeStamp.appName).append('start', timeStamp.startTime)
      .append('end', timeStamp.endTime);
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }

}



