import {NgModule} from '@angular/core';
import {NgxEchartsModule} from 'ngx-echarts';


import {ThemeModule} from '../../../@theme/theme.module';
import {SingleActiveComponent} from './single-active.component';
import {SingleVersionComponent} from './single-version.component';
import {SingleApplicationComponent} from './single-application.component';
import {SingleDayComponent} from './single-day.component';
import {SingleTimeComponent} from './single-time.component';
import {SingleRetentionComponent} from './single-retention.component';
import {SingleTrendComponent} from './single-trend.component';
import {Ng2SearchPipeModule} from 'ng2-search-filter';


const components = [
  SingleVersionComponent,
  SingleActiveComponent,
  SingleApplicationComponent,
  SingleDayComponent,
  SingleTimeComponent,
  SingleRetentionComponent,
  SingleTrendComponent,
];

@NgModule({
  imports: [ThemeModule, NgxEchartsModule, Ng2SearchPipeModule],
  declarations: [...components],
  exports: [...components],
})
export class SinghomeModule {
}



