import {Component, OnInit} from '@angular/core';
import {LocalDataSource} from 'ng2-smart-table';
import {SingleService} from '../single.service';
import {SingleTableService} from '../single-table.service';
import * as moment from 'moment';
import {ReqData, USERTIME, UserTime} from '../single';
import {HttpClient, HttpParams} from '@angular/common/http';
import {TESTDATA, TestData} from './usertime';

declare var $;


@Component({
  selector: 'ngx-usertime',
  templateUrl: './usertime.component.html',
  styleUrls: ['./usertime.component.scss'],
})
export class UsertimeComponent implements OnInit {
  private BaseUrl = 'http://10.0.0.30:8000/';
  options: any = {};
  userTime: UserTime = USERTIME;
  title: string;
  mInstance: any;
  testData: TestData[] = TESTDATA;

  chartInit(ev): void {
    this.mInstance = ev;
    this.singleService.changeDate(this.singleService.getReqData());
  }

  constructor(private service: SingleTableService, private singleService: SingleService, private http: HttpClient) {
    this.title = this.singleService.getReqData().appName;
    const url = this.BaseUrl + 'listAll';
    http.get(url).toPromise().then(x => {
      this.testData = x as TestData[];
      console.log(this.testData);
      this.source.load(this.testData);
    });
    singleService.getDateSubject().subscribe(x => {
      singleService.getUserTime(x).then(z => {
        this.userTime = z as UserTime;
        this.options.series[0].data = this.userTime.data;
        this.mInstance.setOption(this.options);
      });
    });
  }

  ngOnInit() {
    this.dateRangePicker();
    this.options = {
      legend: {
        data: ['使用时段分析'],
        left: 'right',
        show: true,
      },
      polar: {},
      tooltip: {
        formatter: '{a} <br/>{b} :  {c}%',
        trigger: 'axis',
        axisPointer: {
          type: 'shadow',
        },
      },
      angleAxis: {
        type: 'category',
        data: ['12a', '1a', '2a', '3a', '4a', '5a', '6a',
          '7a', '8a', '9a', '10a', '11a',
          '12p', '1p', '2p', '3p', '4p', '5p',
          '6p', '7p', '8p', '9p', '10p', '11p'],
        boundaryGap: false,
        splitLine: {
          show: true,
          lineStyle: {
            color: '#999',
            type: 'dashed',
          },
        },
        axisLine: {
          show: false,
        },
      },
      radiusAxis: {},
      toolbox: {
        show: true,
        orient: 'vertical',
        top: 'center',
        feature: {
          magicType: {show: true, type: ['line', 'bar']},
          restore: {show: true},
          saveAsImage: {show: true},
        },
      },
      dataZoom: [{
        textStyle: {
          color: '#8392A5',
        },
        handleSize: '80%',
        dataBackground: {
          areaStyle: {
            color: '#8392A5',
          },
          lineStyle: {
            opacity: 0.8,
            color: '#8392A5',
          },
        },
        handleStyle: {
          color: '#fff',
          shadowBlur: 3,
          shadowColor: 'rgba(0, 0, 0, 0.6)',
          shadowOffsetX: 2,
          shadowOffsetY: 2,
        },
      }, {
        type: 'inside',
      }],
      series: [{
        name: '用户使用手机时段',
        type: 'bar',
        label: {
          normal: {
            show: true,
          },
        },
        coordinateSystem: 'polar',
        data: this.userTime.data,
      }],
    };
  }

  dateRangePicker() {
    const picker: any = $('#startEndTime');
    const dataRageOption: Object = {
      singleDatePicker: true,
      showDropdowns: true,
      locale: {
        applyClass: 'btn-green',
        applyLabel: '确定',
        cancelLabel: '取消',
        daysOfWeek: ['日', '一', '二', '三', '四', '五', '六'],
        firstDay: 1,
        monthNames: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
        format: 'YYYY-MM-DD',
      },
      maxDate: moment(),
    };
    picker.daterangepicker(dataRageOption, (start, end, label) => {
      console.info(`start:${start.format('YYYY-MM-DD')}, end:${end}, label:${label}`);
      const reqData = new ReqData();
      reqData.reqTime = start.format('YYYY-MM-DD');
      this.singleService.changeDate(reqData);
    });

  }

  settings = {
    actions: false,
    hideSubHeader: true,
    columns: {
      number: {
        title: 'ID',
        type: 'number',
      },
      province: {
        title: '省份',
        type: 'string',
      },
      city: {
        title: '城市',
        type: 'string',
      },
    },
  };

  source: LocalDataSource = new LocalDataSource();
}



