import {Component, OnInit} from '@angular/core';
import {LocalDataSource} from 'ng2-smart-table';
import {SingleService} from '../single.service';
import {SingleTableService} from '../single-table.service';

import * as moment from 'moment';
import {TimeStamp, UserActive, USERACTIVE} from '../single';

declare var $;


@Component({
  selector: 'ngx-useractive',
  templateUrl: './useractive.component.html',
  styleUrls: ['./useractive.component.scss'],
})
export class UseractiveComponent implements OnInit {
  options: any = {};
  useractive: UserActive = USERACTIVE;
  mInstance: any;
  title: string;
  page = 1;
  pageSize = 10;
  maxSize = 3;

  changePage(page: number) {
    console.log(page);
  }

  chartInit(ev): void {
    this.mInstance = ev;
    this.singleService.changeActiveData(this.singleService.getTimeStamp());
  }

  constructor(private service: SingleTableService, private singleService: SingleService) {
    this.title = this.singleService.getTimeStamp().appName;
    const data = this.service.getUserActiveData();
    this.source.load(data);
    singleService.getActiveSubject().subscribe(x => {
      singleService.getUserActive(x).then(z => {
        this.useractive = z as UserActive;
        this.options.xAxis.data = this.useractive.date;
        this.options.series[0].data = this.useractive.clickNum;
        this.mInstance.setOption(this.options);
      });
    });
  }

  ngOnInit() {
    this.dateRangePicker();
    this.options = {
      legend: {
        data: ['用户活跃分析'],
      },
      tooltip: {
        trigger: 'axis',
        formatter: function (params) {
          return params[0].name + '<br/>'
            + '日活跃度 : ' + params[0].value + '<br/>';
        },
      },
      animation: true,
      animationDurationUpdate: 1000,
      animationEasingUpdate: 'cubicInOut',
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: this.useractive.date,
        splitLine: {
          lineStyle: {
            type: 'dashed',
          },
        },
      },
      yAxis: {
        type: 'value',
        splitLine: {
          lineStyle: {
            type: 'dashed',
          },
        },
        axisLabel: {
          formatter: '{value}',
        },
        scale: true,
      },
      dataZoom: [{
        textStyle: {
          color: '#8392A5',
        },
        handleSize: '80%',
        dataBackground: {
          areaStyle: {
            color: '#8392A5',
          },
          lineStyle: {
            opacity: 0.8,
            color: '#8392A5',
          },
        },
        handleStyle: {
          color: '#fff',
          shadowBlur: 3,
          shadowColor: 'rgba(0, 0, 0, 0.6)',
          shadowOffsetX: 2,
          shadowOffsetY: 2,
        },
      }, {
        type: 'inside',
      }],
      toolbox: {
        show: true,
        orient: 'vertical',
        feature: {
          magicType: {show: true, type: ['line', 'bar']},
          restore: {show: true},
          saveAsImage: {show: true},
        },
      },
      series: [{
        name: '用户活跃分析',
        data: this.useractive.clickNum,
        type: 'line',
        label: {
          type: 'text',
          normal: {
            show: true,
            formatter: function (param) {
              return param.data.name;
            },
            position: 'top',
          },
        },
        itemStyle: {
          normal: {
            shadowBlur: 10,
            shadowColor: 'rgba(25, 100, 150, 0.5)',
            shadowOffsetY: 5,
            color: new echarts.graphic.RadialGradient(0.4, 0.3, 1, [{
              offset: 0,
              color: 'rgb(129, 227, 238)',
            }, {
              offset: 1,
              color: 'rgb(25, 183, 207)',
            }]),
          },
        },
      }],
    };
  }

  dateRangePicker() {
    const picker: any = $('#startEndTime');
    const dataRageOption: Object = {
      locale: {
        applyClass: 'btn-green',
        applyLabel: '确定',
        cancelLabel: '取消',
        fromLabel: '开始',
        format: 'YYYY/MM/DD',
        toLabel: '到',
        customRangeLabel: '自定义时间',
        daysOfWeek: ['日', '一', '二', '三', '四', '五', '六'],
        firstDay: 1,
        monthNames: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
      },
      startDate: moment().startOf('month'),
      endDate: moment(),
      maxDate: moment(),
      // minDate: moment().subtract(90, 'days'),
      'ranges': {
        '今日': [
          moment(),
          moment(),
        ],
        '昨日': [
          moment().subtract(1, 'days'),
          moment().subtract(1, 'days'),
        ],
        '本周': [moment().subtract(7, 'days'), moment().subtract(1, 'days')],
        '最近30天': [moment().subtract(30, 'days'), moment().subtract(1, 'days')],
        '本月': [moment().startOf('month'), moment().endOf('month')],
        '上个月': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')],
      },
      'alwaysShowCalendars': true,
      'showCustomRangeLabel': false,
    };
    picker.daterangepicker(dataRageOption, (start, end, label) => {
      console.info(`start:${start.format('YYYY-MM-DD')}, end:${end}, label:${label}`);
      const timeStamp = new TimeStamp();
      timeStamp.startTime = start.format('YYYY-MM-DD');
      timeStamp.endTime = end.format('YYYY-MM-DD');
      this.singleService.changeActiveData(timeStamp);
    });

  }

  settings = {
    actions: false,
    hideSubHeader: true,
    pager: {
      display: false,
    },
    columns: {
      id: {
        title: 'ID',
        type: 'number',
      },
      date: {
        title: '日期',
        type: 'string',
      },
      dayUser: {
        title: '启动用户',
        type: 'number',
      },
      dayActive: {
        title: '日活跃度',
        type: 'string',
      },
      dayMonth: {
        title: '日活/月活',
        type: 'string',
      },
      monthUser: {
        title: '月活跃用户',
        type: 'number',
      },
      monthActive: {
        title: '月活跃度',
        type: 'string',
      },
      monthSilentUser: {
        title: '月沉默用户',
        type: 'number',
      },
      monthSlice: {
        title: '月沉默率',
        type: 'string',
      },
      leaveUser: {
        title: '流失用户',
        type: 'number',
      },
    },
  };

  source: LocalDataSource = new LocalDataSource();
}



