import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {SingleComponent} from './single.component';
import {VersionevoComponent} from './versionevo/versionevo.component';
import {ApplicationComponent} from './application/application.component';
import {DaytimeComponent} from './daytime/daytime.component';
import {RetentionComponent} from './retention/retention.component';
import {UseractiveComponent} from './useractive/useractive.component';
import {UsertimeComponent} from './usertime/usertime.component';
import {SinglehomeComponent} from './singlehome/singlehome.component';
import {TrendComponent} from './trend/trend.component';


const routes: Routes = [{
  path: '',
  component: SingleComponent,
  children: [
    {
      path: 'singlehome',
      component: SinglehomeComponent,
    },
    {
      path: 'versionevo',
      component: VersionevoComponent,
    },
    {
      path: 'application',
      component: ApplicationComponent,
    },
    {
      path: 'daytime',
      component: DaytimeComponent,
    },
    {
      path: 'retention',
      component: RetentionComponent,
    },
    {
      path: 'useractive',
      component: UseractiveComponent,
    },
    {
      path: 'usertime',
      component: UsertimeComponent,
    },
    {
      path: 'trend',
      component: TrendComponent,
    }],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SingleRoutingModule {
}




