import {NgModule} from '@angular/core';

import {ThemeModule} from '../../@theme/theme.module';
import {NgxEchartsModule} from 'ngx-echarts';
import {Ng2SmartTableModule} from 'ng2-smart-table';
import {SingleComponent} from './single.component';
import {SingleRoutingModule} from './single-routing.module';
import {VersionevoComponent} from './versionevo/versionevo.component';
import {ApplicationComponent} from './application/application.component';
import {DaytimeComponent} from './daytime/daytime.component';
import {RetentionComponent} from './retention/retention.component';
import {UseractiveComponent} from './useractive/useractive.component';
import {UsertimeComponent} from './usertime/usertime.component';
import {SinglehomeComponent} from './singlehome/singlehome.component';
import {SinghomeModule} from './singlehome/singhome.module';
import {SingleService} from './single.service';
import {SingleTableService} from './single-table.service';
import {TrendComponent} from './trend/trend.component';
import {Ng2SearchPipeModule} from 'ng2-search-filter';


const routedComponents = [
  SingleComponent,
  VersionevoComponent,
  ApplicationComponent,
  DaytimeComponent,
  RetentionComponent,
  UseractiveComponent,
  UsertimeComponent,
  SinglehomeComponent,
  TrendComponent,
];


@NgModule({
  imports: [
    ThemeModule,
    SingleRoutingModule,
    SinghomeModule,
    NgxEchartsModule,
    Ng2SmartTableModule,
    Ng2SearchPipeModule,
  ],
  declarations: [
    ...routedComponents,
  ],
  providers: [
    SingleTableService,
    SingleService,
  ],
})
export class SingleModule {
}



