import {AfterViewInit, Component} from '@angular/core';
import {TIMESTAMP, TimeStamp, USERACTIVE, UserActive} from '../single';
import {SingleService} from '../single.service';
import * as moment from 'moment';

declare var $;

@Component({
  selector: 'ngx-single-active',
  template: `
    <div echarts [options]="options" (chartInit)="chartInit($event)" theme = "light" class="echart"></div>
  `,
})
export class SingleActiveComponent implements AfterViewInit {
  options: any = {};
  useractive: UserActive = USERACTIVE;
  mInstance: any;

  chartInit(ev): void {
    this.mInstance = ev;
    this.slService.changeActiveData(this.slService.getTimeStamp());
  }

  constructor(private slService: SingleService) {
    slService.getActiveSubject().subscribe(x => {
      slService.getUserActive(x).then(z => {
        this.useractive = z as UserActive;
        this.options.xAxis.data = this.useractive.date;
        this.options.series[0].data = this.useractive.clickNum;
        this.mInstance.setOption(this.options);
      });
    });
  }

  ngAfterViewInit() {
    this.options = {
      legend: {
        data: ['用户活跃分析'],
      },
      tooltip: {
        trigger: 'axis',
        formatter: function (params) {
          return params[0].name + '<br/>'
            + '日活跃度 : ' + params[0].value + ' %<br/>';
        },
      },
      animation: true,
      animationDurationUpdate: 1000,
      animationEasingUpdate: 'cubicInOut',
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: this.useractive.date,
        splitLine: {
          lineStyle: {
            type: 'dashed',
          },
        },
      },
      yAxis: {
        type: 'value',
        splitLine: {
          lineStyle: {
            type: 'dashed',
          },
        },
        axisLabel: {
          formatter: '{value} %',
        },
        scale: true,
      },
      toolbox: {
        show: true,
      },
      series: [{
        name: '用户活跃分析',
        data: this.useractive.clickNum,
        type: 'line',
        label: {
          type: 'text',
          normal: {
            show: true,
            formatter: function (param) {
              return param.data.name;
            },
            position: 'top',
          },
        },
        itemStyle: {
          normal: {
            shadowBlur: 10,
            shadowColor: 'rgba(25, 100, 150, 0.5)',
            shadowOffsetY: 5,
            color: new echarts.graphic.RadialGradient(0.4, 0.3, 1, [{
              offset: 0,
              color: 'rgb(129, 227, 238)',
            }, {
              offset: 1,
              color: 'rgb(25, 183, 207)',
            }]),
          },
        },
      }],
    };
  }
}



