import {AfterViewInit, Component, OnDestroy} from '@angular/core';
import {SERIESDATA, Version} from '../versionevo/version';

@Component({
  selector: 'ngx-single-version',
  template: `
    <div echarts [options]="options" theme = "macarons" class="echart"></div>
  `,
})
export class SingleVersionComponent implements AfterViewInit {
  options: any = {};
  seriesData: Version = SERIESDATA;

  ngAfterViewInit() {
    this.options = {
      backgroundColor: '#fff',
      title: {
        text: '版本应用情况',
        // subtext: '2016年',
        x: 'center',
        y: 'center',
        textStyle: {
          fontWeight: 'normal',
          fontSize: 16,
        },
      },
      tooltip: {
        show: true,
        trigger: 'item',
        formatter: '{b}: {c} ({d}%)',
      },
      legend: {
        orient: 'horizontal',
        bottom: '0%',
        data: this.seriesData.allVersionString,
      },
      series: [{
        type: 'pie',
        selectedMode: 'single',
        radius: ['25%', '58%'],
        // color: ['#86D560', '#AF89D6', '#59ADF3', '#FF999A', '#FFCC67'],

        label: {
          normal: {
            position: 'inner',
            formatter: '{d}%',

            textStyle: {
              color: '#fff',
              fontWeight: 'bold',
              fontSize: 14,
            },
          },
        },
        labelLine: {
          normal: {
            show: false,
          },
        },
        data: this.seriesData.versionNumber,
      }, {
        type: 'pie',
        radius: ['58%', '83%'],
        itemStyle: {
          normal: {
            color: '#F2F2F2',
          },
          emphasis: {
            color: '#ADADAD',
          },
        },
        label: {
          normal: {
            position: 'inner',
            formatter: '{c}人',
            textStyle: {
              color: '#777777',
              fontWeight: 'bold',
              fontSize: 14,
            },
          },
        },
        data: this.seriesData.versionNumber,
      }],
    };
  }
}



