import {Component, OnInit} from '@angular/core';
import {LocalDataSource} from 'ng2-smart-table';
import {SingleService} from '../single.service';
import {User, USERS} from './user';
import {SingleTableService} from '../single-table.service';

@Component({
  selector: 'ngx-application',
  templateUrl: './application.component.html',
  styleUrls: ['./application.component.scss'],
})
export class ApplicationComponent implements OnInit {
  options: any = {};
  users: User[] = USERS;
  selectedUser: User = USERS[0];

  constructor(private service: SingleTableService, singleService: SingleService) {
    const data = this.service.getData();
    this.source.load(data);
  }

  onSelect(user: User): void {
    this.selectedUser = user;
  }

  ngOnInit() {

    this.options = {
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)',
      },
      legend: {
        orient: 'vertical',
        x: 'left',
        data: ['版本2.1', '版本2.2', '版本2.3', '版本2.4', '版本2.5', '版本2.6', '版本2.7', '版本2.8', '版本2.9', '版本2.10'],
      },
      series: [
        {
          name: '访问来源',
          type: 'pie',
          selectedMode: 'single',
          radius: [0, '30%'],

          label: {
            normal: {
              position: 'inner',
            },
          },
          labelLine: {
            normal: {
              show: false,
            },
          },
          data: [
            {value: 335, name: '版本2.1', selected: true},
            {value: 679, name: '版本2.2'},
            {value: 1548, name: '版本2.3'},
          ],
        },
        {
          name: '访问来源',
          type: 'pie',
          radius: ['40%', '55%'],
          label: {
            normal: {
              formatter: '{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c}  {per|{d}%}  ',
              backgroundColor: '#eee',
              borderColor: '#aaa',
              borderWidth: 1,
              borderRadius: 4,
              // shadowBlur:3,
              // shadowOffsetX: 2,
              // shadowOffsetY: 2,
              // shadowColor: '#999',
              // padding: [0, 7],
              rich: {
                a: {
                  color: '#999',
                  lineHeight: 22,
                  align: 'center',
                },
                // abg: {
                //     backgroundColor: '#333',
                //     width: '100%',
                //     align: 'right',
                //     height: 22,
                //     borderRadius: [4, 4, 0, 0]
                // },
                hr: {
                  borderColor: '#aaa',
                  width: '100%',
                  borderWidth: 0.5,
                  height: 0,
                },
                b: {
                  fontSize: 16,
                  lineHeight: 33,
                },
                per: {
                  /* color: '#eee',*/
                  /*backgroundColor: '#334455',*/
                  padding: [2, 4],
                  borderRadius: 2,
                },
              },
            },
          },
          data: [
            {value: 335, name: '版本2.1'},
            {value: 310, name: '版本2.2'},
            {value: 234, name: '版本2.3'},
            {value: 135, name: '版本2.4'},
            {value: 1048, name: '版本2.5'},
            {value: 251, name: '版本2.6'},
            {value: 147, name: '版本2.7'},
            {value: 102, name: '版本2.8'},
          ],
        },
      ],
    };
  }


  settings = {
    actions: false,
    hideSubHeader: true,
    columns: {
      id: {
        title: 'ID',
        type: 'number',
      },
      firstName: {
        title: '日期',
        type: 'string',
      },
      lastName: {
        title: '启动用户',
        type: 'number',
      },
      username: {
        title: '日活跃度',
        type: 'string',
      },
      email: {
        title: '周活跃用户',
        type: 'number',
      },
      age: {
        title: '周活跃度',
        type: 'string',
      },
      mon: {
        title: '月活跃用户',
        type: 'number',
      },
      month: {
        title: '月活跃度',
        type: 'string',
      },
    },
  };

  source: LocalDataSource = new LocalDataSource();
}



