import {Component, OnInit} from '@angular/core';
import {LocalDataSource} from 'ng2-smart-table';
import {SingleService} from '../single.service';
import {SingleTableService} from '../single-table.service';
import {SERIESDATA, Version} from './version';
import * as moment from 'moment';
import {ReqData, REQDATA, TIMESTAMP, TimeStamp} from '../single';

declare var $;

@Component({
  selector: 'ngx-versionevo',
  templateUrl: './versionevo.component.html',
  styleUrls: ['./versionevo.component.scss'],
})
export class VersionevoComponent implements OnInit {
  options: any = {};
  options1: any = {};
  seriesData: Version = SERIESDATA;
  mInstance: any;
  title: string ;

  chartInit(ev): void {
    this.mInstance = ev;
    this.singleService.changeActiveData(this.singleService.getTimeStamp());
  }

  constructor(private service: SingleTableService, private singleService: SingleService) {
    this.title = this.singleService.getTimeStamp().appName;
    const data = this.service.getVersionData();
    this.source.load(data);
    singleService.getActiveSubject().subscribe(x => {
      singleService.getVersion(x).then(z => {
        this.seriesData = z as Version;
        console.log('演化分析数据');
        console.log(this.seriesData);
        this.options.legend.data = this.seriesData.allVersion;
        this.options.xAxis[0].data = this.seriesData.date;
        let k = 0;
        for (const s of this.seriesData.everyDayVersion) {
          this.options.series[k].data = s;
          k++;
        }
        let i = 0;
        for (const s of this.seriesData.allVersion) {
          this.options.series[i].name = s;
          i++;
        }
        this.mInstance.setOption(this.options);
      });
    });
  }

  ngOnInit() {
    this.dateRangePicker();
    this.options1 = {
      backgroundColor: '#fff',
      title: {
        text: '版本应用情况',
        // subtext: '2016年',
        x: 'center',
        y: 'center',
        textStyle: {
          fontWeight: 'normal',
          fontSize: 16,
        },
      },
      tooltip: {
        show: true,
        trigger: 'item',
        formatter: '{b}: {c} ({d}%)',
      },
      legend: {
        orient: 'horizontal',
        bottom: '0%',
        data: this.seriesData.allVersionString,
      },
      series: [{
        type: 'pie',
        selectedMode: 'single',
        radius: ['25%', '58%'],
        // color: ['#86D560', '#AF89D6', '#59ADF3', '#FF999A', '#FFCC67'],

        label: {
          normal: {
            position: 'inner',
            formatter: '{d}%',

            textStyle: {
              color: '#fff',
              fontWeight: 'bold',
              fontSize: 14,
            },
          },
        },
        labelLine: {
          normal: {
            show: false,
          },
        },
        data: this.seriesData.versionNumber,
      }, {
        type: 'pie',
        radius: ['58%', '83%'],
        itemStyle: {
          normal: {
            color: '#F2F2F2',
          },
          emphasis: {
            color: '#ADADAD',
          },
        },
        label: {
          normal: {
            position: 'inner',
            formatter: '{c}人',
            textStyle: {
              color: '#777777',
              fontWeight: 'bold',
              fontSize: 14,
            },
          },
        },
        data: this.seriesData.versionNumber,
      }],
    };
    this.options = {
      tooltip: {
        trigger: 'axis',
      },
      legend: {
        data: this.seriesData.allVersion,
      },
      toolbox: {
        show: true,
        feature: {
          mark: {show: true},
          dataView: {show: true, readOnly: false},
          magicType: {show: true, type: ['line', 'bar', 'stack', 'tiled']},
          restore: {show: true},
          saveAsImage: {show: true},
        },
      },
      calculable: true,
      xAxis: [
        {
          type: 'category',
          boundaryGap: false,
          data: this.seriesData.date,
        },
      ],
      yAxis: [
        {
          type: 'value',
          axisLabel: {
            formatter: '{value} 人',
          },
        },
      ],
      dataZoom: [{
        textStyle: {
          color: '#8392A5',
        },
        handleSize: '80%',
        dataBackground: {
          areaStyle: {
            color: '#8392A5',
          },
          lineStyle: {
            opacity: 0.8,
            color: '#8392A5',
          },
        },
        handleStyle: {
          color: '#fff',
          shadowBlur: 3,
          shadowColor: 'rgba(0, 0, 0, 0.6)',
          shadowOffsetX: 2,
          shadowOffsetY: 2,
        },
      }, {
        type: 'inside',
      }],
      series: [
        {
          name: this.seriesData.allVersion[0],
          type: 'line',
          stack: '总量',
          data: this.seriesData.everyDayVersion[0],
          label: {
            normal: {
              show: true,
              position: 'insideTop',
            },
          },
        },
        {
          name: this.seriesData.allVersion[1],
          type: 'line',
          stack: '总量',
          data: this.seriesData.everyDayVersion[1],
          label: {
            normal: {
              show: true,
              position: 'insideTop',
            },
          },
        },
        {
          name: this.seriesData.allVersion[2],
          type: 'line',
          stack: '总量',
          data: this.seriesData.everyDayVersion[2],
          label: {
            normal: {
              show: true,
              position: 'insideTop',
            },
          },
        },
        {
          name: this.seriesData.allVersion[3],
          type: 'line',
          stack: '总量',
          data: this.seriesData.everyDayVersion[3],
          label: {
            normal: {
              show: true,
              position: 'insideTop',
            },
          },
        },
        {
          name: this.seriesData.allVersion[4],
          type: 'line',
          stack: '总量',
          data: this.seriesData.everyDayVersion[4],
          label: {
            normal: {
              show: true,
              position: 'insideTop',
            },
          },
        },
      ],
    };
  }

  dateRangePicker() {
    const picker: any = $('#startEndTime');
    const dataRageOption: Object = {
      locale: {
        applyClass: 'btn-green',
        applyLabel: '确定',
        cancelLabel: '取消',
        fromLabel: '开始',
        format: 'YYYY/MM/DD',
        toLabel: '到',
        customRangeLabel: '自定义时间',
        daysOfWeek: ['日', '一', '二', '三', '四', '五', '六'],
        firstDay: 1,
        monthNames: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
      },
      startDate: moment().startOf('month'),
      endDate: moment().endOf('month'),
      maxDate: moment(),
      // minDate: moment().subtract(90, 'days'),
      'ranges': {
        '今日': [
          moment(),
          moment(),
        ],
        '昨日': [
          moment().subtract(1, 'days'),
          moment().subtract(1, 'days'),
        ],
        '本周': [moment().subtract(7, 'days'), moment().subtract(1, 'days')],
        '最近30天': [moment().subtract(30, 'days'), moment().subtract(1, 'days')],
        '本月': [moment().startOf('month'), moment().endOf('month')],
        '上个月': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')],
      },
      'alwaysShowCalendars': true,
      'showCustomRangeLabel': false,
    };
    picker.daterangepicker(dataRageOption, (start, end, label) => {
      console.info(`start:${start.format('YYYY-MM-DD')}, end:${end}, label:${label}`);
      const timeStamp = new TimeStamp();
      timeStamp.startTime = start.format('YYYY-MM-DD');
      timeStamp.endTime = end.format('YYYY-MM-DD');
      this.singleService.changeActiveData(timeStamp);
    });

  }

  settings = {
    actions: false,
    hideSubHeader: true,
    columns: {
      id: {
        title: 'ID',
        type: 'number',
      },
      application: {
        title: '版本',
        type: 'string',
      },
      updateUser: {
        title: '升级用户',
        type: 'number',
      },
      newUser: {
        title: '新用户',
        type: 'number',
      },
      startNumber: {
        title: '启动次数',
        type: 'number',
      },
      startUser: {
        title: '启动用户(分布)',
        type: 'string',
      },
      startTime: {
        title: '平均使用时长',
        type: 'string',
      },
    },
  };

  source: LocalDataSource = new LocalDataSource();
}



