export let TRENDDATA: any[][] = [
  [90, 50, 39, 50, 120, 82, 80], /*新用户*/
  [70, 50, 50, 87, 90, 80, 70], /*老用户*/
  [290, 200, 20, 132, 15, 200, 90], /*启动用户*/
];

export class Trend {
  data: string[];
  detailData: any[][];
  propData: number[];
}

export let TREND: Trend = {
  data: ['2018/01/01', '2018/01/02', '2018/01/03', '2018/01/04', '2018/01/05', '2018/01/06', '2018/01/07'],
  detailData: TRENDDATA,
  propData: [60, 20, 25, 45, 36, 60, 82], /*第一个是新用户占比，第二个是老用户，为整个时间戳的占比*/
};



