import {AfterViewInit, Component} from '@angular/core';
import {REQDATA, ReqData, USERTIME, UserTime} from '../single';
import {SingleService} from '../single.service';
import {DatePipe} from '@angular/common';


@Component({
  selector: 'ngx-single-time',
  template: `
    <div echarts [options]="options"  (chartInit)="chartInit($event)" theme = "macarons" class="echart"></div>
  `,
})
export class SingleTimeComponent implements AfterViewInit {
  options: any = {};
  userTime: UserTime = USERTIME;
  reqData: ReqData = REQDATA;
  mInstance: any;
  singleService: SingleService;

  chartInit(ev): void {
    this.mInstance = ev;
    const date = new Date();
    const pipe = new DatePipe('en-US');
    this.reqData.reqTime = pipe.transform(date, 'yyyy-MM-dd');
    this.singleService.changeDate(this.reqData);
  }

  constructor(private slService: SingleService) {
    this.singleService = slService;
    this.slService.getDateSubject().subscribe(x => {
      this.slService.getUserTime(x).then(z => {
        this.userTime = z as UserTime;
        this.options.series[0].data = this.userTime.data;
        this.mInstance.setOption(this.options);
      });
    });
  }

  ngAfterViewInit() {
    this.options = {
      legend: {
        data: ['使用时段分析'],
        left: 'right',
        show: true,
      },
      polar: {},
      tooltip: {
        formatter: '{a} <br/>{b} :  {c}%',
        trigger: 'axis',
        axisPointer: {
          type: 'shadow',
        },
      },
      angleAxis: {
        type: 'category',
        data: ['12a', '1a', '2a', '3a', '4a', '5a', '6a',
          '7a', '8a', '9a', '10a', '11a',
          '12p', '1p', '2p', '3p', '4p', '5p',
          '6p', '7p', '8p', '9p', '10p', '11p'],
        boundaryGap: false,
        splitLine: {
          show: true,
          lineStyle: {
            color: '#999',
            type: 'dashed',
          },
        },
        axisLine: {
          show: false,
        },
      },
      radiusAxis: {},
      toolbox: {
        show: true,
      },
      series: [{
        name: '用户使用手机时段',
        type: 'bar',
        label: {
          normal: {
            show: true,
          },
        },
        coordinateSystem: 'polar',
        data: this.userTime.data,
      }],
    };

  }
}



