import {Component} from '@angular/core';
import {Router} from '@angular/router';
import {Ng2SearchPipe} from 'ng2-search-filter/ng2-search-filter';
import {Observable} from 'rxjs/Observable';
import {SingleService} from '../single.service';

@Component({
  selector: 'ngx-echarts',
  styleUrls: ['./singlehome.component.scss'],
  templateUrl: './singlehome.component.html',
})
export class SinglehomeComponent {
  options: any = {};
  selected: string;

  constructor(private router: Router, private singleService: SingleService) {
    this.selected = singleService.getTimeStamp().appName;
    singleService.getAppNames().then(apps => {
      this.items = apps as string[];
    });
  }

  goDetail() {
    this.router.navigate(['/pages/single/versionevo']);
  }

  content = '';
  show: boolean = false;
  filter = new Ng2SearchPipe();

  change(content: string): void {
    this.show = true;
    this.content = content;
    if (this.filter.transform(this.items, this.content).length === 0)
      this.show = false;
  }

  click(value: string): void {
    this.selected = value;
    this.show = false;
    this.singleService.changeApp(value);
    this.content = '';
  }

  focus() {
    if (this.filter.transform(this.items, this.content).length !== 0)
      this.show = true;
  }

  focusOut() {
    Observable.timer(200).subscribe(x => this.show = false);
  }

  items: string[] = [];
}



