import {AfterViewInit, Component} from '@angular/core';
import {TREND, Trend} from '../trend/trend';

@Component({
  selector: 'ngx-single-trend',
  template: `
    <div  echarts [options]="options" theme = "light" class="echart"></div>
  `,
})
export class SingleTrendComponent implements AfterViewInit {
  options: any = {};
  trend: Trend = TREND;

  ngAfterViewInit() {
    this.options = {
      title: [/*{
        text: '用户人数分析',
        left: '1%',
        top: '6%',
      }, */],
      tooltip: {
        trigger: 'axis',
      },
      legend: {
        x: 150,
        top: '7%',
        textStyle: {
          color: '#ffd285',
        },
        data: ['新用户', '老用户', '启动用户'],
      },
      grid: {
        left: '2%',
        top: '16%',
        bottom: '6%',
        containLabel: true,
      },
      toolbox: {
        'show': false,
        feature: {
          saveAsImage: {},
        },
      },
      xAxis: {
        type: 'category',
        'axisTick': {
          'show': false,
        },
        boundaryGap: false,
        data: this.trend.data,
      },
      yAxis: {
        splitLine: {
          show: true,
        },
        'axisTick': {
          'show': false,
        },
        type: 'value',
        axisLabel: {
          formatter: '{value} 人',
        },
      },
      series: [{
        name: '新用户',
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.trend.detailData[0],
      }, {
        name: '老用户',
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.trend.detailData[1],
      }, {
        name: '启动用户',
        smooth: true,
        type: 'line',
        symbolSize: 8,
        symbol: 'circle',
        data: this.trend.detailData[2],
      }],
    };
  }
}



