import {AfterViewInit, Component} from '@angular/core';
import {ReqData, REQDATA, SERIESDATA, SeriesData} from '../single';
import {DatePipe} from '@angular/common';
import {SingleService} from '../single.service';


@Component({
  selector: 'ngx-single-day',
  template: `
    <div echarts [options]="options" (chartInit)="chartInit($event)" theme="macarons" class="echart"></div>
  `,
})
export class SingleDayComponent implements AfterViewInit {
  options: any = {};
  seriesData: SeriesData = SERIESDATA;
  reqData: ReqData = REQDATA;
  mInstance: any;
  singleService: SingleService;

  chartInit(ev): void {
    this.mInstance = ev;
    /*const date = new Date();
    const pipe = new DatePipe('en-US');
    console.log(pipe.transform(date, 'yyyy-MM-dd'));
    this.reqData.reqTime = pipe.transform(date, 'yyyy-MM-dd');*/
    this.singleService.changeDate(this.reqData);
  }

  constructor(private slService: SingleService) {
    this.singleService = slService;
    this.slService.getDateSubject().subscribe(x => {
      this.slService.getDayTime(x).then(z => {
        this.seriesData = z as SeriesData;
        this.options.series[0].data = this.seriesData.appTime;
        this.mInstance.setOption(this.options);
      });
    });
  }

  ngAfterViewInit() {
    this.options = {
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {d}%',
      },
      legend: {
        orient: 'vertical',
        x: 'left',
      },
      toolbox: {
        show: true,
      },
      series: [
        {
          name: '日使用手机时长分析',
          type: 'pie',
          radius: ['50%', '70%'],
          avoidLabelOverlap: false,
          labelLine: {
            normal: {
              show: true,
            },
          },
          data: this.seriesData.appTime,
        },
      ],
    };
  }
}



